import React, {Component} from 'react';
import Markdown from 'react-markdown';
import {Anchor, Row, Col} from 'antd';

const {Link} = Anchor;

class JsPage extends Component {
    constructor(props) {
        super(props);
    }




    render() {

        return (
            <div style={{background:"#c0d2bc"}}>
                <Row>
                    <Col span={19}>
                        <h3 id="static"> static 关键字 </h3>
                        <Markdown source={"static 关键字主要有以下四种使用场景 \n" +
                        "\n" +
                        "    修饰成员变量和成员方法\n" +
                        "    静态代码块\n" +
                        "    修饰类(只能修饰内部类)\n" +
                        "    静态导包(用来导入类中的静态资源，1.5之后的新特性)\n" +
                        "    修饰成员变量和成员方法(常用)\n" +
                        "**修饰成员变量和成员方法(常用)**\n" +
                        "\n" +
                        "被 static 修饰的成员属于类，不属于单个这个类的某个对象，被类中所有对象共享，可以并且建议通过类名调用。被static 声明的成员变量属于静态成员变量，静态变量 存放在 Java 内存区域的方法区。\n" +
                        "\n" +
                        "方法区与 Java 堆一样，是各个线程共享的内存区域，它用于存储已被虚拟机加载的类信息、常量、静态变量、即时编译器编译后的代码等数据。虽然Java虚拟机规范把方法区描述为堆的一个逻辑部分，但是它却有一个别名叫做 Non-Heap（非堆），目的应该是与 Java 堆区分开来。\n" +
                        "\n" +
                        "HotSpot 虚拟机中方法区也常被称为 “永久代”，本质上两者并不等价。仅仅是因为 HotSpot 虚拟机设计团队用永久代来实现方法区而已，这样 HotSpot 虚拟机的垃圾收集器就可以像管理 Java 堆一样管理这部分内存了。但是这并不是一个好主意，因为这样更容易遇到内存溢出问题。\n" +
                        "\n" +
                        "调用格式：\n" +
                        "\n" +
                        "    类名.静态变量名\n" +
                        "    类名.静态方法名()\n" +
                        "如果变量或者方法被 private 则代表该属性或者该方法只能在类的内部被访问而不能在类的外部被访问。\n" +
                        "\n" +
                        "\n" +
                        "**静态代码块**\n" +
                        "\n" +
                        "静态代码块定义在类中方法外, 静态代码块在非静态代码块之前执行(静态代码块—非静态代码块—构造方法)。 该类不管创建多少对象，静态代码块只执行一次.\n" +
                        "\n" +
                        "一个类中的静态代码块可以有多个，位置可以随便放，它不在任何的方法体内，JVM加载类时会执行这些静态的代码块，如果静态代码块有多个，JVM将按照它们在类中出现的先后顺序依次执行它们，每个代码块只会被执行一次。\n" +
                        "\n" +
                        "静态代码块对于定义在它之后的静态变量，可以赋值，但是不能访问.\n" +
                        "\n" +
                        "\n" +
                        "**静态内部类**\n" +
                        "\n" +
                        "静态内部类与非静态内部类之间存在一个最大的区别，我们知道非静态内部类在编译完成之后会隐含地保存着一个引用，该引用是指向创建它的外围类，但是静态内部类却没有。没有这个引用就意味着：\n" +
                        "\n" +
                        "    它的创建是不需要依赖外围类的创建。\n" +
                        "    它不能使用任何外围类的非static成员变量和方法。\n" +
                        ".\n" +
                        "    \n" +
                        "    public class Singleton {\n" +
                        "        //声明为 private 避免调用默认构造方法创建对象\n" +
                        "        private Singleton() {\n" +
                        "        }\n" +
                        "        \n" +
                        "        //声明为 private 表明静态内部该类只能在该 Singleton 类中被访问\n" +
                        "        private static class SingletonHolder {\n" +
                        "            private static final Singleton INSTANCE = new Singleton();\n" +
                        "        }\n" +
                        "        public static Singleton getUniqueInstance() {\n" +
                        "            return SingletonHolder.INSTANCE;\n" +
                        "        }\n" +
                        "    }\n" +
                        "当 Singleton 类加载时，静态内部类 SingletonHolder 没有被加载进内存。只有当调用 getUniqueInstance() 方法从而触发 SingletonHolder.INSTANCE 时 SingletonHolder 才会被加载，此时初始化 INSTANCE 实例，并且 JVM 能确保 INSTANCE 只被实例化一次。\n" +
                        "\n" +
                        "这种方式不仅具有延迟初始化的好处，而且由 JVM 提供了对线程安全的支持。\n" +
                        "\n" +
                        "**静态导包**\n" +
                        "\n" +
                        "格式为：import static\n" +
                        "\n" +
                        "这两个关键字连用可以指定导入某个类中的指定静态资源，并且不需要使用类名调用类中静态成员，可以直接使用类中静态成员变量和成员方法\n" +
                        "\n" +
                        "    import static java.lang.Math.;\n" +
                        "    //换成import static java.lang.Math.max;具有一样的效果\n" +
                        "    public class Demo {\n" +
                        "    \tpublic static void main(String[] args) {\n" +
                        "    \t\tint max = max(1,2);\n" +
                        "    \t\tSystem.out.println(max);\n" +
                        "    \t}\n" +
                        "    }"}/>


                        <h3 id="final"> final 关键字 </h3>
                        <Markdown source={"final关键字主要用在三个地方：变量、方法、类。\n" +
                        "\n" +
                        "对于一个final变量，如果是基本数据类型的变量，则其数值一旦在初始化之后便不能更改；如果是引用类型的变量，则在对其初始化之后便不能再让其指向另一个对象。\n" +
                        "\n" +
                        "当用final修饰一个类时，表明这个类不能被继承。final类中的所有成员方法都会被隐式地指定为final方法。\n" +
                        "\n" +
                        "使用final方法的原因有两个。第一个原因是把方法锁定，以防任何继承类修改它的含义；第二个原因是效率。在早期的Java实现版本中，会将final方法转为内嵌调用。但是如果方法过于庞大，可能看不到内嵌调用带来的任何性能提升（现在的Java版本已经不需要使用final方法进行这些优化了）。**类中所有的private方法都隐式地指定为final**。\n" +
                        "\n"}/>
                        <h3 id="this"> this 关键字 </h3>
                        <p>this关键字用于引用类的当前实例。</p>

                        <h3 id="super"> super 关键字 </h3>
                        <p>super关键字用于从子类访问父类的变量和方法。 this和super是属于对象范畴的东西，而静态方法是属于类范畴的东西。</p>


                        <h3 id="synchronized"> synchronized 关键字 </h3>
                        <Markdown source={"synchronized关键字最主要的三种使用方式的总结\n" +
                        "\n" +
                        "修饰实例方法，作用于当前对象实例加锁，进入同步代码前要获得当前对象实例的锁\n" +
                        "\n" +
                        "修饰静态方法，作用于当前类对象加锁，进入同步代码前要获得当前类对象的锁 。**也就是给当前类加锁，会作用于类的所有对象实例**，因为静态成员不属于任何一个实例对象，是类成员（ static 表明这是该类的一个静态资源，不管new了多少个对象，只有一份，所以对该类的所有对象都加了锁）。所以如果一个线程A调用一个实例对象的非静态 synchronized 方法，而线程B需要调用这个实例对象所属类的静态 synchronized 方法，是允许的，不会发生互斥现象，因为访问静态 synchronized 方法占用的锁是当前类的锁，而访问非静态 synchronized 方法占用的锁是当前实例对象锁。\n" +
                        "\n" +
                        "修饰代码块，指定加锁对象，对给定对象加锁，进入同步代码库前要获得给定对象的锁。 和 synchronized 方法一样，synchronized(this)代码块也是锁定当前对象的。synchronized 关键字加到 static 静态方法和 synchronized(class)代码块上都是是给 Class 类上锁。这里再提一下：synchronized关键字加到非 static 静态方法上是给对象实例上锁。另外需要注意的是：尽量不要使用 synchronized(String a) 因为JVM中，字符串常量池具有缓冲功能！\n" +
                        "\n" +
                        "双重校验锁实现对象单例:\n" +
                        "\n" +
                        "    public class Singleton {\n" +
                        "        private volatile static Singleton uniqueInstance;\n" +
                        "        private Singleton() {\n" +
                        "        }\n" +
                        "        public static Singleton getUniqueInstance() {\n" +
                        "           //先判断对象是否已经实例过，没有实例化过才进入加锁代码\n" +
                        "            if (uniqueInstance == null) {\n" +
                        "                //类对象加锁\n" +
                        "                synchronized (Singleton.class) {\n" +
                        "                    if (uniqueInstance == null) {\n" +
                        "                        uniqueInstance = new Singleton();\n" +
                        "                    }\n" +
                        "                }\n" +
                        "            }\n" +
                        "            return uniqueInstance;\n" +
                        "        }\n" +
                        "    }\n" +
                        "\n" +
                        "另外，需要注意 uniqueInstance 采用 volatile 关键字修饰也是很有必要。\n" +
                        "\n" +
                        "uniqueInstance 采用 volatile 关键字修饰也是很有必要的， uniqueInstance = new Singleton(); 这段代码其实是分为三步执行：\n" +
                        "\n" +
                        "    1、为 uniqueInstance 分配内存空间\n" +
                        "    2、初始化 uniqueInstance\n" +
                        "    3、将 uniqueInstance 指向分配的内存地址\n" +
                        "\n" +
                        "但是由于 JVM 具有指令重排的特性，执行顺序有可能变成 1->3->2。指令重排在单线程环境下不会出先问题，但是在多线程环境下会导致一个线程获得还没有初始化的实例。例如，线程 T1 执行了 1 和 3，此时 T2 调用 getUniqueInstance() 后发现 uniqueInstance 不为空，因此返回 uniqueInstance，但此时 uniqueInstance 还未被初始化。\n" +
                        "\n" +
                        "使用 volatile 可以禁止 JVM 的指令重排，保证在多线程环境下也能正常运行。\n" +
                        "\n" +
                        "**synchronized 关键字底层原理属于 JVM 层面**\n" +
                        "\n" +
                        "① synchronized 同步语句块的情况\n" +
                        "\n" +
                        "    public class SynchronizedDemo {\n" +
                        "    \tpublic void method() {\n" +
                        "    \t\tsynchronized (this) {\n" +
                        "    \t\t\tSystem.out.println(\"synchronized 代码块\");\n" +
                        "    \t\t}\n" +
                        "    \t}\n" +
                        "    }\n" +
                        "\n" +
                        "通过 JDK 自带的 javap 命令查看 SynchronizedDemo 类的相关字节码信息：首先切换到类的对应目录执行 javac SynchronizedDemo.java 命令生成编译后的 .class 文件，然后执行javap -c -s -v -l SynchronizedDemo.class。\n" +
                        "\n" +
                        "javap是 Java class文件分解器，可以反编译（即对javac编译的文件进行反编译），也可以查看java编译器生成的字节码。用于分解class文件。\n" +
                        "参考http://blog.csdn.net/junsure2012/article/details/7099222\n" +
                        "\n" +
                        "\n"}/>
                        <div align="center"><img style={{maxHeight:"500px"}} src="data:image/png;base64,"/></div>
                        <Markdown source={"从上面我们可以看出：\n" +
                        "\n" +
                        "synchronized 同步语句块的实现使用的是 monitorenter 和 monitorexit 指令，其中 monitorenter 指令指向同步代码块的开始位置，monitorexit 指令则指明同步代码块的结束位置。 当执行 monitorenter 指令时，线程试图获取锁也就是获取 monitor(monitor对象存在于每个Java对象的对象头中，synchronized 锁便是通过这种方式获取锁的，也是为什么Java中任意对象可以作为锁的原因) 的持有权.当计数器为0则可以成功获取，获取后将锁计数器设为1。相应的在执行 monitorexit 指令后，将锁计数器设为0，表明锁被释放。如果获取对象锁失败，那当前线程就要阻塞等待，直到锁被另外一个线程释放为止。\n" +
                        "\n" +
                        "② synchronized 修饰方法的的情况\n" +
                        "\n" +
                        "    public class SynchronizedDemo2 {\n" +
                        "    \tpublic synchronized void method() {\n" +
                        "    \t\tSystem.out.println(\"synchronized 方法\");\n" +
                        "    \t}\n" +
                        "    }"}/>
                        <div align="center"><img style={{maxHeight:"470px"}} src="data:image/png;base64,"/></div>

                        <Markdown source={"synchronized 修饰的方法并没有 monitorenter 指令和 monitorexit 指令，取得代之的确实是 ACC_SYNCHRONIZED 标识，该标识指明了该方法是一个同步方法，JVM 通过该 ACC_SYNCHRONIZED 访问标志来辨别一个方法是否声明为同步方法，从而执行相应的同步调用。\n" +
                        "\n" +
                        "在 Java 早期版本中，synchronized 属于重量级锁，效率低下，因为监视器锁（monitor）是依赖于底层的操作系统的 Mutex Lock 来实现的，Java 的线程是映射到操作系统的原生线程之上的。如果要挂起或者唤醒一个线程，都需要操作系统帮忙完成，而操作系统实现线程之间的切换时需要从用户态转换到内核态，这个状态之间的转换需要相对比较长的时间，时间成本相对较高，这也是为什么早期的 synchronized 效率低的原因。庆幸的是在 Java 6 之后 Java 官方对从 JVM 层面对synchronized 较大优化，所以现在的 synchronized 锁效率也优化得很不错了。JDK1.6对锁的实现引入了大量的优化，如自旋锁、适应性自旋锁、锁消除、锁粗化、偏向锁、轻量级锁等技术来减少锁操作的开销。\n" +
                        "\n" +
                        "JDK1.6 之后的底层优化\n" +
                        "\n" +
                        "JDK1.6 对锁的实现引入了大量的优化，如偏向锁、轻量级锁、自旋锁、适应性自旋锁、锁消除、锁粗化等技术来减少锁操作的开销。\n" +
                        "\n" +
                        "锁主要存在四中状态，依次是：无锁状态、偏向锁状态、轻量级锁状态、重量级锁状态，他们会随着竞争的激烈而逐渐升级。注意锁可以升级不可降级，这种策略是为了提高获得锁和释放锁的效率。\n" +
                        "\n" +
                        "①偏向锁\n" +
                        "\n" +
                        "引入偏向锁的目的和引入轻量级锁的目的很像，他们都是为了没有多线程竞争的前提下，减少传统的重量级锁使用操作系统互斥量产生的性能消耗。但是不同是：轻量级锁在无竞争的情况下使用 CAS 操作去代替使用互斥量。而偏向锁在无竞争的情况下会把整个同步都消除掉。\n" +
                        "\n" +
                        "偏向锁的“偏”就是偏心的偏，它的意思是会偏向于**第一个获得它的线程，如果在接下来的执行中，该锁没有被其他线程获取，那么持有偏向锁的线程就不需要进行同步！**关于偏向锁的原理可以查看《深入理解Java虚拟机：JVM高级特性与最佳实践》第二版的13章第三节锁优化。\n" +
                        "\n" +
                        "但是对于锁竞争比较激烈的场合，偏向锁就失效了，因为这样场合极有可能每次申请锁的线程都是不相同的，因此这种场合下不应该使用偏向锁，否则会得不偿失，需要注意的是，偏向锁失败后，并不会立即膨胀为重量级锁，而是先升级为轻量级锁。\n" +
                        "\n" +
                        "② 轻量级锁\n" +
                        "\n" +
                        "倘若偏向锁失败，虚拟机并不会立即升级为重量级锁，它还会尝试使用一种称为轻量级锁的优化手段(1.6之后加入的)。轻量级锁不是为了代替重量级锁，它的本意是在没有多线程竞争的前提下，减少传统的重量级锁使用操作系统互斥量产生的性能消耗，因为使用轻量级锁时，不需要申请互斥量。另外，轻量级锁的加锁和解锁都用到了CAS操作。 关于轻量级锁的加锁和解锁的原理可以查看《深入理解Java虚拟机：JVM高级特性与最佳实践》第二版的13章第三节锁优化。\n" +
                        "\n" +
                        "轻量级锁能够提升程序同步性能的依据是“对于绝大部分锁，在整个同步周期内都是不存在竞争的”，这是一个经验数据。如果没有竞争，轻量级锁使用 CAS 操作避免了使用互斥操作的开销。但如果存在锁竞争，除了互斥量开销外，还会额外发生CAS操作，因此在有锁竞争的情况下，轻量级锁比传统的重量级锁更慢！如果锁竞争激烈，那么轻量级将很快膨胀为重量级锁！\n" +
                        "\n" +
                        "③ 自旋锁和自适应自旋\n" +
                        "\n" +
                        "轻量级锁失败后，虚拟机为了避免线程真实地在操作系统层面挂起，还会进行一项称为自旋锁的优化手段。\n" +
                        "\n" +
                        "互斥同步对性能最大的影响就是阻塞的实现，因为挂起线程/恢复线程的操作都需要转入内核态中完成（用户态转换到内核态会耗费时间）。\n" +
                        "\n" +
                        "一般线程持有锁的时间都不是太长，所以仅仅为了这一点时间去挂起线程/恢复线程是得不偿失的。 所以，虚拟机的开发团队就这样去考虑：“我们能不能让后面来的请求获取锁的线程等待一会而不被挂起呢？看看持有锁的线程是否很快就会释放锁”。为了让一个线程等待，我们只需要让线程执行一个忙循环（自旋），这项技术就叫做自旋。\n" +
                        "\n" +
                        "百度百科对自旋锁的解释：\n" +
                        "\n" +
                        "何谓自旋锁？它是为实现保护共享资源而提出一种锁机制。其实，自旋锁与互斥锁比较类似，它们都是为了解决对某项资源的互斥使用。无论是互斥锁，还是自旋锁，在任何时刻，最多只能有一个保持者，也就说，在任何时刻最多只能有一个执行单元获得锁。但是两者在调度机制上略有不同。对于互斥锁，如果资源已经被占用，资源申请者只能进入睡眠状态。但是自旋锁不会引起调用者睡眠，如果自旋锁已经被别的执行单元保持，调用者就一直循环在那里看是否该自旋锁的保持者已经释放了锁，\"自旋\"一词就是因此而得名。\n" +
                        "\n" +
                        "自旋锁在 JDK1.6 之前其实就已经引入了，不过是默认关闭的，需要通过--XX:+UseSpinning参数来开启。JDK1.6及1.6之后，就改为默认开启的了。需要注意的是：自旋等待不能完全替代阻塞，因为它还是要占用处理器时间。如果锁被占用的时间短，那么效果当然就很好了！反之，相反！自旋等待的时间必须要有限度。如果自旋超过了限定次数任然没有获得锁，就应该挂起线程。自旋次数的默认值是10次，用户可以修改--XX:PreBlockSpin来更改。\n" +
                        "\n" +
                        "另外,在 JDK1.6 中引入了自适应的自旋锁。自适应的自旋锁带来的改进就是：自旋的时间不在固定了，而是和前一次同一个锁上的自旋时间以及锁的拥有者的状态来决定，虚拟机变得越来越“聪明”了。\n" +
                        "\n" +
                        "④ 锁消除\n" +
                        "\n" +
                        "锁消除理解起来很简单，它指的就是虚拟机即使编译器在运行时，如果检测到那些共享数据不可能存在竞争，那么就执行锁消除。锁消除可以节省毫无意义的请求锁的时间。\n" +
                        "\n" +
                        "⑤ 锁粗化\n" +
                        "\n" +
                        "原则上，我们再编写代码的时候，总是推荐将同步快的作用范围限制得尽量小——只在共享数据的实际作用域才进行同步，这样是为了使得需要同步的操作数量尽可能变小，如果存在锁竞争，那等待线程也能尽快拿到锁。\n" +
                        "\n" +
                        "大部分情况下，上面的原则都是没有问题的，但是如果一系列的连续操作都对同一个对象反复加锁和解锁，那么会带来很多不必要的性能消耗。\n" +
                        "\n" +
                        "Synchronized 和 ReenTrantLock 的对比\n" +
                        "\n" +
                        "① 两者都是可重入锁\n" +
                        "\n" +
                        "两者都是可重入锁。“可重入锁”概念是：自己可以再次获取自己的内部锁。比如一个线程获得了某个对象的锁，此时这个对象锁还没有释放，当其再次想要获取这个对象的锁的时候还是可以获取的，如果不可锁重入的话，就会造成死锁。同一个线程每次获取锁，锁的计数器都自增1，所以要等到锁的计数器下降为0时才能释放锁。\n" +
                        "\n" +
                        "② synchronized 依赖于 JVM 而 ReenTrantLock 依赖于 API\n" +
                        "\n" +
                        "synchronized 是依赖于 JVM 实现的，前面我们也讲到了 虚拟机团队在 JDK1.6 为 synchronized 关键字进行了很多优化，但是这些优化都是在虚拟机层面实现的，并没有直接暴露给我们。ReenTrantLock 是 JDK 层面实现的（也就是 API 层面，需要 lock() 和 unlock 方法配合 try/finally 语句块来完成），所以我们可以通过查看它的源代码，来看它是如何实现的。\n" +
                        "\n" +
                        "③ ReenTrantLock 比 synchronized 增加了一些高级功能\n" +
                        "\n" +
                        "相比synchronized，ReenTrantLock增加了一些高级功能。主要来说主要有三点：①等待可中断；②可实现公平锁；③可实现选择性通知（锁可以绑定多个条件）\n" +
                        "\n" +
                        "ReenTrantLock提供了一种能够中断等待锁的线程的机制，通过lock.lockInterruptibly()来实现这个机制。也就是说正在等待的线程可以选择放弃等待，改为处理其他事情。\n" +
                        "\n" +
                        "ReenTrantLock可以指定是公平锁还是非公平锁。而synchronized只能是非公平锁。**所谓的公平锁就是先等待的线程先获得锁**。 ReenTrantLock默认情况是非公平的，可以通过 ReenTrantLock类的ReentrantLock(boolean fair)构造方法来制定是否是公平的。\n" +
                        "\n" +
                        "synchronized关键字与wait()和notify/notifyAll()方法相结合可以实现等待/通知机制，ReentrantLock类当然也可以实现，但是需要借助于Condition接口与newCondition() 方法。Condition是JDK1.5之后才有的，它具有很好的灵活性，比如可以实现多路通知功能也就是在一个Lock对象中可以创建多个Condition实例（即对象监视器），线程对象可以注册在指定的Condition中，从而可以有选择性的进行线程通知，在调度线程上更加灵活。 在使用notify/notifyAll()方法进行通知时，被通知的线程是由 JVM 选择的，用ReentrantLock类结合Condition实例可以实现“选择性通知” ，这个功能非常重要，而且是Condition接口默认提供的。而synchronized关键字就相当于整个Lock对象中只有一个Condition实例，所有的线程都注册在它一个身上。如果执行notifyAll()方法的话就会通知所有处于等待状态的线程这样会造成很大的效率问题，而Condition实例的signalAll()方法 只会唤醒注册在该Condition实例中的所有等待线程。\n" +
                        "\n" +
                        "如果你想使用上述功能，那么选择ReenTrantLock是一个不错的选择。\n" +
                        "\n" +
                        "④ 性能已不是选择标准\n" +
                        "\n" +
                        "在JDK1.6之前，synchronized 的性能是比 ReenTrantLock 差很多。具体表示为：synchronized 关键字吞吐量岁线程数的增加，下降得非常严重。而ReenTrantLock 基本保持一个比较稳定的水平。我觉得这也侧面反映了， synchronized 关键字还有非常大的优化余地。后续的技术发展也证明了这一点，我们上面也讲了在 JDK1.6 之后 JVM 团队对 synchronized 关键字做了很多优化。JDK1.6 之后，synchronized 和 ReenTrantLock 的性能基本是持平了。所以网上那些说因为性能才选择 ReenTrantLock 的文章都是错的！JDK1.6之后，性能已经不是选择synchronized和ReenTrantLock的影响因素了！而且虚拟机在未来的性能改进中会更偏向于原生的synchronized，所以还是提倡在synchronized能满足你的需求的情况下，优先考虑使用synchronized关键字来进行同步！优化后的synchronized和ReenTrantLock一样，在很多地方都是用到了CAS操作。"}/>

                        <h3 id="volatile"> volatile 关键字 </h3>
                        <Markdown source={"一旦一个共享变量（类的成员变量、类的静态成员变量）被volatile修饰之后，那么就具备了两层语义：\n" +
                        "\n" +
                        "　1）保证了不同线程对这个变量进行操作时的可见性，即一个线程修改了某个变量的值，这新值对其他线程来说是立即可见的。\n" +
                        "\n" +
                        "　2）禁止进行指令重排序。\n" +
                        "\n" +
                        "volatile具有可见性、有序性，不具备原子性。"}/>


                    </Col>
                    <Col span={5}>
                        <Anchor showInkInFixed="true" style={{background:"#ebefe0"}}>
                            <Link href="#static" title="static 关键字"/>
                            <Link href="#final" title="final 关键字"/>
                            <Link href="#this" title="this 关键字"/>
                            <Link href="#super" title="super 关键字"/>
                            <Link href="#synchronized" title=" synchronized 关键字"/>
                            <Link href="#volatile" title=" volatile 关键字"/>
                        </Anchor>
                    </Col>
                </Row>
            </div>

        );
    }
}

export default JsPage;


